// font
@font-face {
  font-family: "NimbusSanL";
  src: url("../fonts/NimbusSanL-Reg.eot?#iefix") format("embedded-opentype"),
    url("../fonts/NimbusSanL-Reg.otf") format("opentype"),
    url("../fonts/NimbusSanL-Reg.woff") format("woff"),
    url("../fonts/NimbusSanL-Reg.ttf") format("truetype"),
    url("../fonts/NimbusSanL-Reg.svg#NimbusSanL-Reg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "NimbusSanL";
  src: url("../fonts/NimbusSanL-Bol.eot?#iefix") format("embedded-opentype"),
    url("../fonts/NimbusSanL-Bol.otf") format("opentype"),
    url("../fonts/NimbusSanL-Bol.woff") format("woff"),
    url("../fonts/NimbusSanL-Bol.ttf") format("truetype"),
    url("../fonts/NimbusSanL-Bol.svg#NimbusSanL-Bol") format("svg");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Myriad Pro";
  src: url("../fonts/MyriadPro-Light.eot?#iefix") format("embedded-opentype"),
    url("../fonts/MyriadPro-Light.otf") format("opentype"),
    url("../fonts/MyriadPro-Light.woff") format("woff"),
    url("../fonts/MyriadPro-Light.ttf") format("truetype"),
    url("../fonts/MyriadPro-Light.svg#MyriadPro-Light") format("svg");
  font-weight: 300;
  font-style: normal;
}

body {
  color: $black;
  font-size: 17px;
  font-family: $font-family-base;
  //font-weight: $font-weight-base;
  line-height: $line-height-base*1.2;
  font-weight: 100;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
h1 {
  font-family: $font-family-head;
  font-size: 2em;
}

h2 {
  font-weight: $font-family-head;
  font-size: 1.5em;
}
h3 {
  font-size: 1.25em;
}
h4 {
  font-size: 1em;
}

// Headings
//

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  margin-bottom: $headings-margin-bottom;
  font-family: $font-family-head;
  line-height: $headings-line-height;
  color: $black;
  font-weight: 700;

  small {
    color: inherit;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;
}

p {
  margin-top: 0;
  margin-bottom: $paragraph-margin-bottom;
  color: inherit;
  font-family: inherit;
  padding: 0;
}

ol,
ul {
  margin-top: 0;
  margin-bottom: $paragraph-margin-bottom;
}

b,
strong {
  font-weight: bold;
}

@media (max-width: $reflex-xs) {
  body {
    font-size: 14px;
  }
}
