  visibility: visible;
  opacity: 1;
  z-index: 100; }

.modal-dialog {
  position: relative;
  max-width: 800px;
  max-height: 80vh;
  border-radius: 2px;
  background: #fff;
  overflow: auto;
  cursor: default; }

