.alert {
	position: relative;
	padding: .75rem 1.25rem;
	margin-bottom: 1rem;

&.alert-success {
	color: #155724;
	background-color: #d4edda;
	border-color: #c3e6cb;
}
&.alert-danger {
	color: #721c24;
	background-color: #f8d7da;
	border-color: #f5c6cb;
}
&.alert-warning {
	color: #856404;
	background-color: #fff3cd;
	border-color: #ffeeba;
}
&.alert-info {
	color: #0c5460;
	background-color: #d1ecf1;
	border-color: #bee5eb;
}
}